// Multiple web page source:
// https://medium.com/@ipenywis/intro-to-react-router-for-beginners-multiple-page-apps-461f4729bd3f

import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";

//Pages
import MainPage from "./_pages";
import AboutMePage from "./_pages/AboutMe";
import NotFoundPage from "./_pages/NotFound";
import MenuPage from "./_pages/Menu";
import Projects from "./_pages/Projects";
import Header from "./_pages/Header";

const App = (props) => {
  return (
    <Router>
      <div className="App">
        <Header />
        <Switch>
          <Route exact path="/" component={MainPage} />
          <Route exact path="/Menu" component={MenuPage} />
          <Route exact path="/AboutMe" component={AboutMePage} />
          <Route exact path="/Projects" component={Projects} />
          <Route exact path="/404Error" component={NotFoundPage} />
          <Redirect to="/404Error" />
        </Switch>
      </div>
    </Router>
  );
};

export default App;
